<template>
  <div :class="{ hidden: hidden }" class="flex flex-align flex-jus pagination-container">
    <el-pagination
      id="pagination"
      :page-size="tempPageSize"
      :page-sizes="pageSizes"
      :total="total"
      v-bind="$attrs"
      :pager-count="5"
      :layout="layout"
      :current-page="tempCurrentPage"
      @current-change="handleCurrentChange"
    >
      <template>
        <span class="pagination__total pagination___text">{{ 'Total:' + total }}</span>
        <template v-if="showPageSize">
          <el-select v-model="tempPageSize" size="mini" style="width: 76px; margin-left: 16px; margin-right: 10px">
            <el-option v-for="item in pageSizes" :key="item" :label="`${item}`" :value="item"> </el-option>
          </el-select>
          <span class="show-title">items/page</span>
        </template>
      </template>
    </el-pagination>
    <div class="go-to-buttom" v-if="layout.indexOf('jumper') !== -1">
      <button @click="goToHandle">GO</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PortalPagination',
  props: {
    total: {
      required: true,
      type: Number,
    },
    currentPage: {
      type: Number,
      default: 1,
    },
    pageSize: {
      type: Number,
      default: 10,
    },
    pageSizes: {
      type: Array,
      default() {
        return [10, 20, 50, 100];
      },
    },
    layout: {
      type: String,
      default: 'slot, prev, pager, next, jumper',
    },
    // 是否显示 pageSize选择框
    showPageSize: {
      type: Boolean,
      default: true,
    },
    hidden: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {};
  },
  computed: {
    tempCurrentPage: {
      get() {
        return this.currentPage;
      },
      set(val) {
        this.$emit('update:currentPage', val);
      },
    },
    tempPageSize: {
      get() {
        return this.pageSize;
      },
      set(val) {
        this.$emit('update:pageSize', val);
        this.handleSizeChange(val);
      },
    },
  },
  methods: {
    handleSizeChange(val) {
      this.tempCurrentPage = 1;
      this.$emit('pagination', { pageIndex: 1, pageSize: val });
    },
    handleCurrentChange(val) {
      this.tempCurrentPage = val;
      this.$emit('pagination', { pageIndex: val, pageSize: this.pageSize });
    },
    goToHandle() {},
  },
};
</script>

