import { getAmountUnitFun } from '@/views/business-center/pages/settlement/api/revenue-settlement';

class AmountUnit {
  constructor() {
    this.amountUnitList = '';
    this.initAmountUnit();
  }
  // 数字字符串转数字金额
  format(string, currencyCode) {
    let amountUnit = '';
    let num = Number(string);
    let money = parseFloat(num.toFixed(2)).toLocaleString();
    let charIndex = money.lastIndexOf('.');
    if (this.amountUnitList !== '') {
      for (let item in this.amountUnitList) {
        if (item == 'en;1005;' + currencyCode) {
          amountUnit = this.amountUnitList[item].symbol;
        }
      }
    } else {
      amountUnit = '';
    }
    if (charIndex === -1) {
      return amountUnit + money + '.00';
    }
    let moneyLength = money.slice(charIndex + 1).length;
    return `${amountUnit + money}${moneyLength === 1 ? '0' : ''}`;
  }

  getCurrency(currencyCode) {
    if (this.amountUnitList !== '') {
      let currency;
      for (let item in this.amountUnitList) {
        if (item === `en;1005;${currencyCode}`) {
          currency = this.amountUnitList[item];
          break;
        }
      }
      return currency;
    }
  }

  async initAmountUnit() {
    this.amountUnitList = await getAmountUnitFun();
  }
}

export default new AmountUnit();
