<template>
  <div class="table-page-container">
    <div class="add-button" v-if="showAddButton">
      <el-button class="template-add" icon="el-icon-plus" type="primary" size="small" @click="showAddDialog()">
        Add
      </el-button>
      <el-dialog :title="createTitle" :visible.sync="isShowAddDialog" width="448px" :before-close="closeAddDialog">
        <slot name="addDialogContext"></slot>
        <span slot="footer" class="dialog-footer">
          <el-button @click="closeAddDialog">cancel</el-button>
          <el-button type="primary" @click="addSubmit">submit</el-button>
        </span>
      </el-dialog>
    </div>
    <div :class="{ 'table-region': true, 'no-data-fixed': !tableData.length }" style="height: 100%">
      <el-table
        ref="tableRef"
        empty-text="No records found."
        :header-cell-style="headerStyle"
        :class="{ 'mini-table': isSingleCheck }"
        v-loading="loading"
        :data="tableData"
        :size="size"
        :border="border"
        v-bind="$attrs"
        :row-key="idKey"
        v-on="listeners"
        :key="tableKey"
        :max-height="maxHeight"
      >
        <el-table-column
          v-if="isCheck"
          align="center"
          width="30"
          type="selection"
          :reserve-selection="isCheckMemory"
          :selectable="checkSelectable"
        >
        </el-table-column>
        <template v-for="(item, index) in columns">
          <el-table-column
            :sortable="sortFun(item)"
            :key="index"
            v-bind="item"
            :width="item.width || ''"
            :show-overflow-tooltip="
              [true, false].includes(item.showOverflowTooltip) ? item.showOverflowTooltip : showOverflowTooltip
            "
            :formatter="item.formatter || formatterHandle"
          >
            <template v-if="item.__slotName" v-slot="scope">
              <slot :name="item.__slotName" :data="scope"></slot>
            </template>
          </el-table-column>
        </template>
        <template slot="empty">
          <slot name="emptya"></slot>
        </template>
      </el-table>
    </div>
    <div class="pagination-region" v-if="hasPagination && pagination.total > 0">
      <portal-pagination
        id="tablePagination"
        :total="pagination.total"
        :currentPage.sync="pagination.pageIndex"
        :pageSize.sync="pagination.pageSize"
        @pagination="handleSizeChange"
        :pageSizes="pageSizes"
        :layout="pagination.layout"
      />
    </div>
  </div>
</template>

<script>
import PortalPagination from '@/components/portal-pagination/index';
export default {
  name: 'TablePage',
  components: {
    PortalPagination,
  },
  props: {
    showOverflowTooltip: {
      type: Boolean,
      default: true,
    },
    createTitle: {
      type: String,
    },
    showAddButton: {
      type: Boolean,
      default: false,
    },
    submitNewDataFun: {
      type: Function,
      default: () => {},
    },
    checkSelectable: {
      type: Function,
      default: () => {
        return true;
      },
    },
    // 表格列
    columns: {
      type: Array,
      default: () => [],
    },
    // 表格规格大小
    size: {
      type: String,
      default: 'medium',
    },
    // 对组件外部暴露一个 isCheckMemory 属性,默认false, 需要跨页，设置成true
    isCheck: {
      type: Boolean,
      default: false,
    },

    /**
     * 是否需要跨页勾选
     */
    isCheckMemory: {
      type: Boolean,
      default: false,
    },
    isSingleCheck: {
      // 是否单选
      type: Boolean,
      default: false,
    },

    /**
     * 表格行数据的唯一键
     */
    idKey: {
      type: String,
      default: 'id',
    },
    // props 外部传递一个sortArr,告诉组件哪一些字段需要排序
    sortArr: {
      type: Array,
      default: () => [],
    },

    /**
     * 分页需要传入后台的字段
     */
    pageParam: {
      type: Object,
      default: () => {
        return {
          pageOffset: 'pageNo',
          pageSize: 'pageSize',
        };
      },
    },
    // 是否分页
    hasPagination: {
      type: Boolean,
      default: true,
    },
    pagination: {
      type: Object,
      default: () => {
        return {
          total: 0,
          pageIndex: 1,
        };
      }, // 分页参数 === pageSize:每页展示的条数，pageIndex:当前页
    },
    // 每页显示页数集合
    pageSizes: {
      type: Array,
      default() {
        return [10, 20, 50, 100];
      },
    },
    layout: {
      type: String,
      default: 'total, sizes, prev, pager, next, jumper',
    },
    // 获取table数据方法
    getTableData: {
      type: Function,
      default: null,
    },
    outSideTableData: {
      type: Array,
      default: () => [],
    },
    isSearch: {
      type: Boolean,
      default: false,
    },
    initializingLoading: {
      type: Boolean,
      default: true,
    },
    border: {
      // 表格是否带边框
      type: Boolean,
      default: true,
    },
    initChecks: {
      type: Array,
      default: () => [],
    },
    maxHeight: {
      type: [Number, String],
    },
  },
  data() {
    return {
      isShowAddDialog: false,
      order: {
        sortName: '',
        sortOrder: '',
      },
      // 表格数据
      tableData: [],
      tableKey: new Date().getTime(),
      loading: false,
      headerStyle: {
        'background-color': `${this.GlobalStyleVariables.fillLightColor}`,
        'font-size': '14px',
        'font-family': 'HuaweiSans-Medium',
        'font-weight': 500,
        color: `${this.GlobalStyleVariables.textEmphasisColor}`,
      },
      singleSelectData: [],
    };
  },
  computed: {
    listeners: function () {
      let vm = this;
      return Object.assign({}, this.$listeners, {
        // 监听事件 selection-change
        'selection-change': function (val) {
          // do something
          // 表格多选
          if (!vm.isSingleCheck) {
            vm.$emit('selection-change', val);
          }
        },
        select: function (selection, row) {
          // 表格单选
          const __table = vm.$refs.tableRef;
          if (vm.isSingleCheck && selection.includes(row)) {
            __table.clearSelection();
            __table.toggleRowSelection(row, true);
            vm.singleSelectData = row;
          } else {
            vm.singleSelectData = [];
          }
          vm.$emit('single-selection-change', vm.singleSelectData);
        },
        // 监听事件 sort-change
        'sort-change': function (column) {
          vm.order.sortName = column.prop;
          switch (column.order) {
            case 'ascending':
              vm.order.sortOrder = 'asc';
              break;
            case 'descending':
              vm.order.sortOrder = 'desc';
              break;
            default:
              vm.order.sortOrder = '';
          }
          vm.$emit('sortChange', vm.order);
        },
      });
    },
  },
  watch: {
    isSearch() {
      if (this.isSearch) {
        this.$refs.tableRef.clearSelection();
        // 搜索区域刷新表格
        this.queryData();
      }
    },
    outSideTableData: {
      handler(value) {
        if (value) {
          this.tableData = value;
          this.tableKey = new Date().getTime();
          this.pagination.pageIndex = 1;
          this.pagination.total = 0;
        }
      },
      immediate: true,
      deep: true,
    },
    initChecks: {
      handler(value) {
        if (this.$refs.tableRef) {
          this.$refs.tableRef.clearSelection();
        }
        if (value && value.length) {
          for (const key in this.tableData) {
            if (value) {
              value.forEach((check) => {
                if (check[this.idKey] === this.tableData[key][this.idKey]) {
                  this.$refs.tableRef.toggleRowSelection(this.tableData[key], true);
                }
              });
            }
          }
        }
      },
      immediate: true,
      deep: true,
    },
  },
  created() {
    if (this.initializingLoading) {
      this.queryData().then(() => {
        this.$refs.tableRef.clearSelection();
        if (this.initChecks && this.initChecks.length) {
          this.$nextTick(() => {
            this.initChecks.forEach((check) => {
              for (const key in this.tableData) {
                if (check[this.idKey] === this.tableData[key][this.idKey]) {
                  this.$refs.tableRef.toggleRowSelection(this.tableData[key], true);
                }
              }
            });
          });
        }
      });
    } else {
      this.pagination.pageIndex = 1;
      this.pagination.total = 0;
    }
  },
  mounted() {},
  methods: {
    addSubmit() {
      const $this = this;
      const callback = function () {
        $this.closeAddDialog();
      };
      this.submitNewDataFun(callback);
    },
    closeAddDialog() {
      this.isShowAddDialog = false;
    },
    showAddDialog() {
      this.isShowAddDialog = true;
    },

    /**
     * 切换分页数量
     * @param { Number } pageSize 页数
     */
    handleSizeChange(page) {
      // 修改列表每页大小，触发查询
      this.pagination.pageIndex = page.pageIndex;
      this.pagination.pageSize = page.pageSize;
      // 判断是否有数据  避免没数据的时候切换pageSize页面报错
      this.queryData();
    },
    sortFun(item) {
      // 排序
      const sortable = this.sortArr && this.sortArr.indexOf(item.prop) > -1;
      if (sortable) {
        if (!item['sort-method']) {
          return 'custom';
        }
        return true;
      }
      return false;
    },
    async queryData(isReset) {
      if (this.getTableData) {
        this.loading = true;
        this.pagination.pageIndex = !isReset ? this.pagination.pageIndex : 1;
        let param = !this.hasPagination
          ? {}
          : Object.assign(
              {},
              {
                [this.pageParam.pageOffset]: this.pagination.pageIndex,
                [this.pageParam.pageSize]: this.pagination.pageSize,
              }
            );
        if (this.order.sortName && this.order.sortOrder) {
          param.sortName = this.order.sortName;
          param.sortOrder = this.order.sortOrder;
        }
        let result = null;
        try {
          this.$refs.tableRef && this.$refs.tableRef.clearSort(); // 每次请求接口重置排序条件
          result = await this.getTableData(param);
        } catch (e) {
          this.loading = false;
        } finally {
          this.loading = false;
          // 根据后端返回数据结构情况而定
          if (result && result.header.success) {
            this.pagination.total = result.totalRecords || 0;
            this.tableData = Array.from(result.list) || [];
            this.$emit('fetchData', result);
          } else {
            this.tableData = [];
            this.pagination.pageIndex = 1;
            this.pagination.total = 0;
          }
        }
      }
    },

    /**
     * 默认格式化
     */
    formatterHandle(row, column) {
      const property = column.property;
      let newRow = row;
      let propertyVal = '';
      let propertyArray = property && property.split('.');
      propertyArray.forEach((pf) => {
        if (typeof newRow[pf] === 'object') {
          newRow = newRow[pf];
        } else {
          propertyVal = newRow[pf];
        }
      });
      if (propertyVal !== '' && propertyVal !== null && propertyVal !== undefined) {
        return propertyVal;
      }
      return '-';
    },
  },
};
</script>
<style lang="scss" scoped>
