        display: inline-block;
        margin-right: 8px;
      }
      .el-link {
        vertical-align: baseline;
        margin-left: 12px;
      }
    }
    /deep/ .el-table-column--selection .cell {
      padding-left: 16px !important;
      padding-right: 0 !important;
    }
    /deep/ .el-table_1_column_1 .cell {
      padding-left: 16px !important;
    }
    //表头cell后加一个小竖线
    /deep/ .el-table__header-wrapper {
      .el-table__header {
        .has-gutter {
          tr {
            th {
              background-color: $--fill_color--light !important;
              .cell {
                color: $--text_color--emphasis;
                font-family: 'HuaweiSans-Medium';
              }
            }
          }
        }
      }
      table thead tr {
        th div.cell::after {
          content: '';
          display: inline-block;
          position: absolute;
          right: 0;
          top: 5px;
          width: 1px;
          height: 16px;
          background-color: $--fill_color--dark;
        }
        //最后一个不用显示边线
        th:nth-last-of-type(2) div.cell::after {
          display: none;
        }
        .el-table-column--selection div.cell::after {
          content: none;
        }
      }
    }
    //更改排序小三角形的颜色
    /deep/ .el-table {
      .caret-wrapper {
        height: 19px;
      }
      .sort-caret {
        border-width: 4px;
      }
      .sort-caret.ascending {
        top: 0;
      }
      .sort-caret.descending {
        bottom: 1px;
      }
    }
    /deep/ .el-table .ascending .sort-caret.ascending {
      border-bottom-color: $--fill_color--emphasis;
    }
    /deep/ .el-table .descending .sort-caret.descending {
      border-top-color: $--fill_color--emphasis;
    }
    // border表格支持拖拽，样式调整
    /deep/ .el-table--border {
      border: none;
    }
    /deep/ .el-table--border .el-table__cell {
      border-right: none;
    }
    /deep/ .el-table--border::after {
      height: 0;
    }
  }
  .no-data-fixed {
    /deep/ .el-table__fixed,
    /deep/ .el-table__fixed-right {
      box-shadow: 0px -2px 6px opacityColor($--black_color, 0.19);
      height: 40px !important;
    }

    /deep/ .el-table__body-wrapper.is-scrolling-left ~ .el-table__fixed {
      box-shadow: none;
    }
    /deep/ .el-table__body-wrapper.is-scrolling-right ~ .el-table__fixed-right {
      box-shadow: none;
    }
  }

  .pagination-region {
    padding-top: 12px;
  }

  /deep/ .el-table--enable-row-hover {
    .el-table__body {
      tr:hover > td {
        button {
